"use client"

import { useState, useMemo } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import {
  ArrowLeft,
  X,
  Mail,
  Phone,
  Contact,
  Trophy,
  FileText,
  Calendar,
  MapPin,
  Users,
  MoreVertical,
  Eye,
  Download,
  Check,
  CheckCircle2,
  Trash2,
  FileSignature,
  AlertCircle,
} from "lucide-react"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import { InfoCard } from "@/components/users/info-card"
import { ApplicationStatusBadge } from "./application-status-badge"
import { StatusBadge } from "@/components/documents/status-badge"
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Checkbox } from "@/components/ui/checkbox"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger, DropdownMenuSeparator } from "@/components/ui/dropdown-menu"
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog"
import type { Application, ApplicationDocument } from "@/types/application"
import { useToast } from "@/hooks/use-toast"
import { DocumentPreviewModal } from "@/components/documents/document-preview-modal"
import type { Document } from "@/types/document"

interface ApplicationProfileProps {
  application: Application
  onReject?: (application: Application) => void
  onAccept?: (application: Application) => void
}

export function ApplicationProfile({ application, onReject, onAccept }: ApplicationProfileProps) {
  const router = useRouter()
  const { toast } = useToast()
  const [selectedDocIds, setSelectedDocIds] = useState<string[]>([])
  const [rejectDialogOpen, setRejectDialogOpen] = useState(false)
  const [acceptDialogOpen, setAcceptDialogOpen] = useState(false)
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false)
  const [docToDelete, setDocToDelete] = useState<ApplicationDocument | null>(null)
  const [docFilter, setDocFilter] = useState<"all" | "signed" | "unsigned">("all")
  const [selectedDocument, setSelectedDocument] = useState<Document | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [showSigningPanel, setShowSigningPanel] = useState(false)

  const documents = application.documents || []
  
  const filteredDocuments = useMemo(() => {
    if (docFilter === "all") return documents
    return documents.filter((doc) => doc.status === docFilter)
  }, [documents, docFilter])

  const handleRejectClick = () => {
    setRejectDialogOpen(true)
  }

  const handleRejectConfirm = () => {
    if (onReject) {
      onReject(application)
    }
    setRejectDialogOpen(false)
    router.push("/applications")
    toast({
      title: "Заявка отклонена",
      variant: "success",
    })
  }

  const handleAcceptClick = () => {
    setAcceptDialogOpen(true)
  }

  const handleAcceptConfirm = () => {
    if (onAccept) {
      onAccept(application)
    }
    setAcceptDialogOpen(false)
    router.push("/applications")
    toast({
      title: "Заявка принята",
      variant: "success",
    })
  }

  const handleSelectAllDocs = () => {
    if (selectedDocIds.length === filteredDocuments.length) {
      setSelectedDocIds([])
    } else {
      setSelectedDocIds(filteredDocuments.map((doc) => doc.id))
    }
  }

  const handleSelectDoc = (id: string) => {
    if (selectedDocIds.includes(id)) {
      setSelectedDocIds(selectedDocIds.filter((docId) => docId !== id))
    } else {
      setSelectedDocIds([...selectedDocIds, id])
    }
  }

  // Преобразование ApplicationDocument в Document для модального окна
  const convertToDocument = (doc: ApplicationDocument): Document => {
    return {
      id: doc.id,
      name: doc.name,
      type: doc.type,
      status: doc.status,
      date: doc.date,
      size: doc.size,
      athlete: {
        initials: application.athlete.initials,
        fullName: application.athlete.fullName,
      },
      competition: application.competition.name,
      discipline: application.competition.discipline || "",
    }
  }

  const handleDocumentClick = (doc: ApplicationDocument) => {
    const document = convertToDocument(doc)
    setSelectedDocument(document)
    setShowSigningPanel(doc.status === "unsigned")
    setIsModalOpen(true)
  }

  const handleSign = (doc: ApplicationDocument) => {
    const document = convertToDocument(doc)
    setSelectedDocument(document)
    setShowSigningPanel(true)
    setIsModalOpen(true)
  }

  const handleCloseModal = () => {
    setIsModalOpen(false)
    setSelectedDocument(null)
    setShowSigningPanel(false)
  }

  const handleDocumentSigned = () => {
    // Обновить статус документа на "signed"
    // В реальном приложении это будет API вызов
    toast({
      title: "Документ подписан",
      variant: "success",
    })
    handleCloseModal()
  }

  const handleDeleteClick = (doc: ApplicationDocument) => {
    setDocToDelete(doc)
    setDeleteDialogOpen(true)
  }

  const handleDeleteConfirm = () => {
    if (docToDelete) {
      // Удалить документ
      // В реальном приложении это будет API вызов
      toast({
        title: "Документ удален",
        variant: "success",
      })
      setSelectedDocIds(selectedDocIds.filter((id) => id !== docToDelete.id))
    }
    setDeleteDialogOpen(false)
    setDocToDelete(null)
  }

  const handleBulkDownload = () => {
    // Скачать выбранные документы
    toast({
      title: `Скачивание ${selectedDocIds.length} документов`,
      variant: "success",
    })
  }

  const handleBulkDelete = () => {
    // Удалить выбранные документы
    toast({
      title: `Удаление ${selectedDocIds.length} документов`,
      variant: "success",
    })
    setSelectedDocIds([])
  }

  const formatSubmissionDate = (date: string) => {
    return `Заявка от ${date}`
  }

  const athleteAge = application.athlete.age || null
  const athleteBirthDate = application.athlete.birthDate || null
  const athleteInfo = athleteAge && athleteBirthDate ? `${athleteAge} года • ${athleteBirthDate}` : null

  return (
    <div className="min-h-screen bg-gray-50/50">
      <div className="max-w-[896px] mx-auto px-8 py-6">
        <div className="space-y-8">
          {/* Back Button */}
          <div>
            <Link
              href="/applications"
              className="inline-flex items-center gap-2 text-sm font-medium text-gray-600 hover:text-gray-900 transition-colors"
            >
              <ArrowLeft className="h-4 w-4" />
              Вернуться к заявкам
            </Link>
          </div>

          {/* Main Card */}
          <div className="bg-white border border-gray-200 rounded-[10px] p-6 shadow-sm">
          {/* Header */}
          <div className="flex items-start justify-between mb-3 pb-3 border-b border-gray-200">
            <div className="flex-1">
              <div className="flex items-center gap-3 mb-1.5">
                <h1 className="text-base font-semibold text-gray-900">#{application.applicationId}</h1>
                <ApplicationStatusBadge status={application.status} />
              </div>
              <p className="text-sm text-gray-600">{formatSubmissionDate(application.submissionDate)}</p>
            </div>
            {application.status === "pending" && (
              <div className="flex items-center gap-4">
                <Button
                  onClick={handleAcceptClick}
                  className="gap-2 bg-green-500 hover:bg-green-600 text-white"
                >
                  <Check className="h-4 w-4" />
                  Принять заявку
                </Button>
                <Button
                  variant="outline"
                  onClick={handleRejectClick}
                  className="gap-2"
                >
                  <X className="h-4 w-4" />
                  Отклонить заявку
                </Button>
              </div>
            )}
            {application.status === "rejected" && (
              <Button
                onClick={handleAcceptClick}
                className="gap-2 bg-green-500 hover:bg-green-600 text-white"
              >
                <Check className="h-4 w-4" />
                Принять заявку
              </Button>
            )}
            {application.status === "accepted" && (
              <Button
                variant="outline"
                onClick={handleRejectClick}
                className="gap-2"
              >
                <X className="h-4 w-4" />
                Отклонить заявку
              </Button>
            )}
          </div>

          {/* Athlete and Competition Info */}
          <div className="flex items-center gap-2 pt-3">
            {/* Athlete */}
            <div className="flex items-center gap-2 flex-shrink-0">
              <Avatar className="h-9 w-9 flex-shrink-0">
                <AvatarFallback className="bg-gray-100 text-gray-700 text-xs">
                  {application.athlete.initials}
                </AvatarFallback>
              </Avatar>
              <div className="min-w-0">
                <p className="text-xs font-semibold text-gray-900 whitespace-nowrap leading-tight">{application.athlete.fullName}</p>
                {athleteInfo && (
                  <p className="text-xs text-gray-600 whitespace-nowrap leading-tight">{athleteInfo}</p>
                )}
              </div>
            </div>

            {/* Divider */}
            <div className="w-px h-6 bg-gray-200 flex-shrink-0" />

            {/* Competition */}
            <div className="flex items-center gap-1.5 flex-shrink-0">
              <div className="flex items-center justify-center w-6 h-6 bg-gray-50 rounded-lg flex-shrink-0">
                <Trophy className="h-3.5 w-3.5 text-gray-600" />
              </div>
              <div className="min-w-0">
                <p className="text-xs text-gray-600 leading-tight">Соревнование</p>
                <p className="text-xs font-semibold text-gray-900 whitespace-nowrap leading-tight">{application.competition.name}</p>
              </div>
            </div>

            {/* Competition Discipline */}
            {application.competition.discipline && (
              <>
                <div className="w-px h-6 bg-gray-200 flex-shrink-0" />
                <div className="flex-shrink-0">
                  <p className="text-xs text-gray-600 leading-tight">Дисциплина</p>
                  <p className="text-xs font-semibold text-gray-900 whitespace-nowrap leading-tight">{application.competition.discipline}</p>
                </div>
              </>
            )}

            {/* Dates */}
            <div className="w-px h-6 bg-gray-200 flex-shrink-0" />
            <div className="flex items-center gap-1.5 flex-shrink-0">
              <div className="flex items-center justify-center w-6 h-6 bg-gray-50 rounded-lg flex-shrink-0">
                <Calendar className="h-3.5 w-3.5 text-gray-600" />
              </div>
              <div className="min-w-0">
                <p className="text-xs text-gray-600 leading-tight">Даты проведения</p>
                <p className="text-xs font-semibold text-gray-900 whitespace-nowrap leading-tight">{application.competition.dateRange}</p>
              </div>
            </div>
          </div>
        </div>

          {/* Two Column Layout */}
          <div className="grid grid-cols-2 gap-8">
          {/* Contact Information */}
          <InfoCard icon={Contact} title="Контактная информация">
            {application.athlete.email && (
              <div className="flex items-center gap-2 py-2">
                <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg flex-shrink-0">
                  <Mail className="h-4 w-4 text-gray-600" />
                </div>
                <div className="flex-1">
                  <p className="text-sm text-gray-600">Email</p>
                  <p className="text-sm font-semibold text-gray-900">{application.athlete.email}</p>
                </div>
              </div>
            )}
            {application.athlete.phone && (
              <div className="flex items-center gap-2 py-2">
                <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg flex-shrink-0">
                  <Phone className="h-4 w-4 text-gray-600" />
                </div>
                <div className="flex-1">
                  <p className="text-sm text-gray-600">Телефон</p>
                  <p className="text-sm font-semibold text-gray-900">{application.athlete.phone}</p>
                </div>
              </div>
            )}
          </InfoCard>

          {/* Competition Details */}
          <InfoCard icon={Trophy} title="О соревновании">
            {application.competition.participants !== undefined && (
              <div className="flex items-center gap-2 py-2">
                <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg flex-shrink-0">
                  <Users className="h-4 w-4 text-gray-600" />
                </div>
                <div className="flex-1">
                  <p className="text-sm text-gray-600">Участников</p>
                  <p className="text-sm font-semibold text-gray-900">{application.competition.participants}</p>
                </div>
              </div>
            )}
            {application.competition.location && (
              <div className="flex items-center gap-2 py-2">
                <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg flex-shrink-0">
                  <MapPin className="h-4 w-4 text-gray-600" />
                </div>
                <div className="flex-1">
                  <p className="text-sm text-gray-600">Место проведения</p>
                  <p className="text-sm font-semibold text-gray-900">{application.competition.location}</p>
                </div>
              </div>
            )}
          </InfoCard>
        </div>

          {/* Documents Card */}
          <div className="bg-white border border-gray-200 rounded-[10px] shadow-sm">
            {/* Documents Header */}
            <div className="px-6 pt-6 pb-3 border-b border-gray-200">
              <div className="flex items-center gap-3">
                <div className="flex items-center justify-center w-8 h-8 bg-gray-50 rounded-lg">
                  <FileText className="h-4 w-4 text-gray-600" />
                </div>
                <h3 className="text-base font-semibold text-gray-900">Документы</h3>
              </div>
            </div>
            <div className="p-6 pt-0">
              {/* Tabs */}
          <Tabs value={docFilter} onValueChange={(v) => setDocFilter(v as "all" | "signed" | "unsigned")} className="mb-6">
            <TabsList className="mb-6">
              <TabsTrigger value="all">Все</TabsTrigger>
              <TabsTrigger value="signed">Подписанные</TabsTrigger>
              <TabsTrigger value="unsigned">Не подписанные</TabsTrigger>
            </TabsList>

            <TabsContent value="all" className="mt-0">
              {filteredDocuments.length > 0 ? (
                <div className="border border-gray-200 rounded-lg overflow-hidden">
                  <Table>
                    <TableHeader>
                      <TableRow className={selectedDocIds.length > 0 ? "border-b bg-accent/50" : "border-b"}>
                        <TableHead className="w-[44px] px-4">
                          <Checkbox
                            checked={selectedDocIds.length === filteredDocuments.length && filteredDocuments.length > 0}
                            onCheckedChange={handleSelectAllDocs}
                          />
                        </TableHead>
                        <TableHead className="px-4">
                          {selectedDocIds.length > 0 ? (
                            <div className="flex items-center gap-4">
                              <span className="text-sm font-medium">Выбрано: {selectedDocIds.length}</span>
                              <Button variant="ghost" size="sm" className="gap-2 h-8" onClick={handleBulkDownload}>
                                <Download className="h-4 w-4" />
                                Скачать выбранные
                              </Button>
                              <Button variant="ghost" size="sm" className="gap-2 h-8 text-destructive hover:text-destructive" onClick={handleBulkDelete}>
                                <Trash2 className="h-4 w-4" />
                                Удалить
                              </Button>
                            </div>
                          ) : (
                            <span className="text-sm font-medium">Документ</span>
                          )}
                        </TableHead>
                        <TableHead className="px-4">
                          <span className="text-sm font-medium">Статус</span>
                        </TableHead>
                        <TableHead className="w-[60px] px-4"></TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {filteredDocuments.map((doc) => (
                        <TableRow key={doc.id} className="border-b last:border-b-0">
                          <TableCell className="px-4" onClick={(e) => e.stopPropagation()}>
                            <Checkbox
                              checked={selectedDocIds.includes(doc.id)}
                              onCheckedChange={() => handleSelectDoc(doc.id)}
                            />
                          </TableCell>
                          <TableCell className="px-4">
                            <div className="flex items-center gap-3">
                              <div className={`flex items-center justify-center w-8 h-8 rounded-lg ${
                                doc.status === "signed" ? "bg-emerald-50" : "bg-orange-50"
                              }`}>
                                <FileText className={`h-4 w-4 ${
                                  doc.status === "signed" ? "text-emerald-600" : "text-orange-600"
                                }`} />
                              </div>
                              <div>
                                <p className="text-sm font-medium text-gray-900">{doc.name}</p>
                                <p className="text-xs text-gray-500">
                                  {doc.type} • {doc.date || ""} • {doc.size}
                                </p>
                              </div>
                            </div>
                          </TableCell>
                          <TableCell className="px-4">
                            <StatusBadge status={doc.status} />
                          </TableCell>
                          <TableCell className="px-4" onClick={(e) => e.stopPropagation()}>
                            <DropdownMenu>
                              <DropdownMenuTrigger asChild>
                                <Button variant="ghost" size="icon" className="h-8 w-8">
                                  <MoreVertical className="h-4 w-4" />
                                </Button>
                              </DropdownMenuTrigger>
                              <DropdownMenuContent align="end">
                                {doc.status === "signed" ? (
                                  <>
                                    <DropdownMenuItem onClick={() => handleDocumentClick(doc)}>
                                      <Eye className="h-4 w-4 mr-2" />
                                      Посмотреть
                                    </DropdownMenuItem>
                                    <DropdownMenuItem>
                                      <Download className="h-4 w-4 mr-2" />
                                      Скачать
                                    </DropdownMenuItem>
                                    <DropdownMenuSeparator />
                                    <DropdownMenuItem 
                                      variant="destructive" 
                                      className="data-[variant=destructive]:focus:bg-red-50"
                                      onClick={() => handleDeleteClick(doc)}
                                    >
                                      <Trash2 className="h-4 w-4 mr-2" />
                                      Удалить
                                    </DropdownMenuItem>
                                  </>
                                ) : (
                                  <>
                                    <DropdownMenuItem onClick={() => handleSign(doc)}>
                                      <FileSignature className="h-4 w-4 mr-2" />
                                      Подписать
                                    </DropdownMenuItem>
                                    <DropdownMenuItem>
                                      <Download className="h-4 w-4 mr-2" />
                                      Скачать
                                    </DropdownMenuItem>
                                  </>
                                )}
                              </DropdownMenuContent>
                            </DropdownMenu>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </div>
              ) : (
                <div className="text-center py-12 text-sm text-gray-500">
                  Документы не найдены
                </div>
              )}
            </TabsContent>
            <TabsContent value="signed" className="mt-0">
              {(() => {
                const signedDocs = filteredDocuments.filter(doc => doc.status === "signed")
                return signedDocs.length > 0 ? (
                  <div className="border border-gray-200 rounded-lg overflow-hidden">
                    <Table>
                      <TableHeader>
                        <TableRow className={selectedDocIds.length > 0 ? "border-b bg-accent/50" : "border-b"}>
                          <TableHead className="w-[44px] px-4">
                            <Checkbox
                              checked={selectedDocIds.length === signedDocs.length && signedDocs.length > 0}
                              onCheckedChange={() => {
                                if (selectedDocIds.length === signedDocs.length) {
                                  setSelectedDocIds([])
                                } else {
                                  setSelectedDocIds(signedDocs.map((doc) => doc.id))
                                }
                              }}
                            />
                          </TableHead>
                          <TableHead className="px-4">
                            {selectedDocIds.length > 0 ? (
                              <div className="flex items-center gap-4">
                                <span className="text-sm font-medium">Выбрано: {selectedDocIds.length}</span>
                                <Button variant="ghost" size="sm" className="gap-2 h-8" onClick={handleBulkDownload}>
                                  <Download className="h-4 w-4" />
                                  Скачать выбранные
                                </Button>
                                <Button variant="ghost" size="sm" className="gap-2 h-8 text-destructive hover:text-destructive" onClick={handleBulkDelete}>
                                  <Trash2 className="h-4 w-4" />
                                  Удалить
                                </Button>
                              </div>
                            ) : (
                              <span className="text-sm font-medium">Документ</span>
                            )}
                          </TableHead>
                          <TableHead className="px-4">
                            <span className="text-sm font-medium">Статус</span>
                          </TableHead>
                          <TableHead className="w-[60px] px-4"></TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {signedDocs.map((doc) => (
                          <TableRow key={doc.id} className="border-b last:border-b-0">
                            <TableCell className="px-4" onClick={(e) => e.stopPropagation()}>
                              <Checkbox
                                checked={selectedDocIds.includes(doc.id)}
                                onCheckedChange={() => handleSelectDoc(doc.id)}
                              />
                            </TableCell>
                            <TableCell className="px-4">
                              <div className="flex items-center gap-3">
                                <div className="flex items-center justify-center w-8 h-8 bg-emerald-50 rounded-lg">
                                  <FileText className="h-4 w-4 text-emerald-600" />
                                </div>
                                <div>
                                  <p className="text-sm font-medium text-gray-900">{doc.name}</p>
                                  <p className="text-xs text-gray-500">
                                    {doc.type} • {doc.date || ""} • {doc.size}
                                  </p>
                                </div>
                              </div>
                            </TableCell>
                            <TableCell className="px-4">
                              <StatusBadge status={doc.status} />
                            </TableCell>
                            <TableCell className="px-4" onClick={(e) => e.stopPropagation()}>
                              <DropdownMenu>
                                <DropdownMenuTrigger asChild>
                                  <Button variant="ghost" size="icon" className="h-8 w-8">
                                    <MoreVertical className="h-4 w-4" />
                                  </Button>
                                </DropdownMenuTrigger>
                                <DropdownMenuContent align="end">
                                  <DropdownMenuItem onClick={() => handleDocumentClick(doc)}>
                                    <Eye className="h-4 w-4 mr-2" />
                                    Посмотреть
                                  </DropdownMenuItem>
                                  <DropdownMenuItem>
                                    <Download className="h-4 w-4 mr-2" />
                                    Скачать
                                  </DropdownMenuItem>
                                  <DropdownMenuSeparator />
                                  <DropdownMenuItem 
                                    variant="destructive" 
                                    className="data-[variant=destructive]:focus:bg-red-50"
                                    onClick={() => handleDeleteClick(doc)}
                                  >
                                    <Trash2 className="h-4 w-4 mr-2" />
                                    Удалить
                                  </DropdownMenuItem>
                                </DropdownMenuContent>
                              </DropdownMenu>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </div>
                ) : (
                  <div className="text-center py-12 text-sm text-gray-500">
                    Документы не найдены
                  </div>
                )
              })()}
            </TabsContent>
            <TabsContent value="unsigned" className="mt-0">
              {(() => {
                const unsignedDocs = filteredDocuments.filter(doc => doc.status === "unsigned")
                return unsignedDocs.length > 0 ? (
                  <div className="border border-gray-200 rounded-lg overflow-hidden">
                    <Table>
                      <TableHeader>
                        <TableRow className={selectedDocIds.length > 0 ? "border-b bg-accent/50" : "border-b"}>
                          <TableHead className="w-[44px] px-4">
                            <Checkbox
                              checked={selectedDocIds.length === unsignedDocs.length && unsignedDocs.length > 0}
                              onCheckedChange={() => {
                                if (selectedDocIds.length === unsignedDocs.length) {
                                  setSelectedDocIds([])
                                } else {
                                  setSelectedDocIds(unsignedDocs.map((doc) => doc.id))
                                }
                              }}
                            />
                          </TableHead>
                          <TableHead className="px-4">
                            {selectedDocIds.length > 0 ? (
                              <div className="flex items-center gap-4">
                                <span className="text-sm font-medium">Выбрано: {selectedDocIds.length}</span>
                                <Button variant="ghost" size="sm" className="gap-2 h-8" onClick={handleBulkDownload}>
                                  <Download className="h-4 w-4" />
                                  Скачать выбранные
                                </Button>
                                <Button variant="ghost" size="sm" className="gap-2 h-8 text-destructive hover:text-destructive" onClick={handleBulkDelete}>
                                  <Trash2 className="h-4 w-4" />
                                  Удалить
                                </Button>
                              </div>
                            ) : (
                              <span className="text-sm font-medium">Документ</span>
                            )}
                          </TableHead>
                          <TableHead className="px-4">
                            <span className="text-sm font-medium">Статус</span>
                          </TableHead>
                          <TableHead className="w-[60px] px-4"></TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {unsignedDocs.map((doc) => (
                          <TableRow key={doc.id} className="border-b last:border-b-0">
                            <TableCell className="px-4" onClick={(e) => e.stopPropagation()}>
                              <Checkbox
                                checked={selectedDocIds.includes(doc.id)}
                                onCheckedChange={() => handleSelectDoc(doc.id)}
                              />
                            </TableCell>
                            <TableCell className="px-4">
                              <div className="flex items-center gap-3">
                                <div className="flex items-center justify-center w-8 h-8 bg-orange-50 rounded-lg">
                                  <FileText className="h-4 w-4 text-orange-600" />
                                </div>
                                <div>
                                  <p className="text-sm font-medium text-gray-900">{doc.name}</p>
                                  <p className="text-xs text-gray-500">
                                    {doc.type} • {doc.date || ""} • {doc.size}
                                  </p>
                                </div>
                              </div>
                            </TableCell>
                            <TableCell className="px-4">
                              <StatusBadge status={doc.status} />
                            </TableCell>
                            <TableCell className="px-4" onClick={(e) => e.stopPropagation()}>
                              <DropdownMenu>
                                <DropdownMenuTrigger asChild>
                                  <Button variant="ghost" size="icon" className="h-8 w-8">
                                    <MoreVertical className="h-4 w-4" />
                                  </Button>
                                </DropdownMenuTrigger>
                                <DropdownMenuContent align="end">
                                  <DropdownMenuItem onClick={() => handleSign(doc)}>
                                    <FileSignature className="h-4 w-4 mr-2" />
                                    Подписать
                                  </DropdownMenuItem>
                                  <DropdownMenuItem>
                                    <Download className="h-4 w-4 mr-2" />
                                    Скачать
                                  </DropdownMenuItem>
                                </DropdownMenuContent>
                              </DropdownMenu>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </div>
                ) : (
                  <div className="text-center py-12 text-sm text-gray-500">
                    Документы не найдены
                  </div>
                )
              })()}
            </TabsContent>
          </Tabs>
            </div>
          </div>
        </div>
      </div>

      {/* Accept Dialog */}
      <AlertDialog open={acceptDialogOpen} onOpenChange={setAcceptDialogOpen}>
        <AlertDialogContent className="sm:max-w-[512px]">
          <AlertDialogHeader>
            <div className="flex items-center gap-2">
              <CheckCircle2 className="h-5 w-5 text-green-500 flex-shrink-0" />
              <AlertDialogTitle className="text-left">Принять заявку?</AlertDialogTitle>
            </div>
            <AlertDialogDescription className="text-left mt-2">
              Система автоматически сгенерирует пакет документов для подписания. 
              Спортсмен будет включен в список участников соревнования.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter className="sm:justify-end">
            <AlertDialogCancel>Отмена</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleAcceptConfirm}
              className="bg-green-500 hover:bg-green-600 text-white"
            >
              Принять
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Reject Dialog */}
      <AlertDialog open={rejectDialogOpen} onOpenChange={setRejectDialogOpen}>
        <AlertDialogContent className="sm:max-w-[512px]">
          <AlertDialogHeader>
            <div className="flex items-center gap-2">
              <X className="h-5 w-5 text-red-500 flex-shrink-0" />
              <AlertDialogTitle className="text-left">Отклонить заявку?</AlertDialogTitle>
            </div>
            <AlertDialogDescription className="text-left mt-2">
              Спортсмен получит уведомление об отклонении заявки. 
              Это действие можно будет отменить.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter className="sm:justify-end">
            <AlertDialogCancel>Отмена</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleRejectConfirm}
              className="bg-red-600 hover:bg-red-700 text-white"
            >
              Отклонить
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  )
}
